

.containerHome{

    
    background-image: url('https://images.unsplash.com/photo-1580927752452-89d86da3fa0a?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Nnx8Y29kaW5nfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=1500&q=60');
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 100vh;
        /* padding: 0.2rem; */
   display: flex;
   align-items: flex-end;
   justify-content: space-between;
   text-align: center;    
}



.leftContainer , .rightContainer{

    width: 100%;
    /* border: 3px solid red; */
    background-color: rgba(255, 255, 255, 0.575);
    height:100%;
    display: flex;
    
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1.5rem auto;
}
.leftContainer  h3 {

    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: white;
    font-size: 180%;
   

}
.leftContainer  h3 span {

    font-family: cursive;
    color: #c10037; 
    /* color: teal; */
    font-size: 100%;
    

}
.leftContainer  h1 {

    
     color: black ;  
     /* color: teal;  */
    font-size: 200%;

}
.leftContainer  p{

    
    margin-top: 20px;

}
.leftContainer  a {

 
    width: 25%;
    padding: 0.5rem 0.5rem;
    margin: 1rem;
    text-align: center;
    color: black;
    font-weight: 600;
    font-family: cursive;
    border: 2px solid black;
    border-radius: 0.25rem;
    background-color: transparent;
    transition: all 1s  ;
    text-decoration: none;

}
.leftContainer  a:hover {

 
   background-color: #fff;
   border: 2px solid #fff;

}


.rightContainer div img{

        
       
        /* border-top-left-radius: 2rem;
        border-top-right-radius: 2rem;
        border-bottom-left-radius: 2rem;
        border-bottom-right-radius: 2rem; */
        width: 270px;
        border-radius: 50%;
        border: 1px solid black;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(241, 235, 235) 0px 0px 0px 3px;
        

        /* box-shadow: rgba(221, 221, 233, 0.25) 0px 50px 100px -20px, rgba(235, 229, 229, 0.3) 0px 30px 60px -30px, rgba(222, 228, 235, 0.35) 0px -2px 6px 0px inset; */
}


@media all and (max-width: 800px) {

.containerHome{

    flex-direction: column;
    height: 120vh;
}

.leftContainer , .rightContainer{

    width: 100%;
    padding-top: 2.5rem;
    /* border: 3px solid red; */
    flex-direction: column;
    height: 50%;
    justify-content: space-around;
    align-items: center;
    padding: 0 auto;
    font-size: large;

}




.leftContainer  button {

 
    width: 25%;
    padding: 0.25rem;
    margin: 1rem;
    color: #8c0c3c ;
    font-weight: 500;
    font-family: cursive;
    border: 2px solid #8c0c3c;
    border-radius: 0.25rem;
    background-color: transparent;
    transition: all 1s  ease-in-out;

}


.rightContainer div {
      width: 250px;
      /* border: 1px solid red; */


        /* box-shadow: rgba(221, 221, 233, 0.25) 0px 50px 100px -20px, rgba(235, 229, 229, 0.3) 0px 30px 60px -30px, rgba(222, 228, 235, 0.35) 0px -2px 6px 0px inset; */
}
.rightContainer div img{

   
    width: 100%;
        border-top-left-radius: 2rem;
        border-top-right-radius: 2rem;
        border-bottom-left-radius: 2rem;
        border-bottom-right-radius: 2rem;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(241, 235, 235) 0px 0px 0px 3px;

        /* box-shadow: rgba(221, 221, 233, 0.25) 0px 50px 100px -20px, rgba(235, 229, 229, 0.3) 0px 30px 60px -30px, rgba(222, 228, 235, 0.35) 0px -2px 6px 0px inset; */
}

}