.navbarMainContainer {
    display: flex;
    align-items: center;
    justify-content: space-around;
    /* text-align: center; */
    font-size: 1.5rem;
    
    color: #4c525a;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    background-color: #c10037; 
    color: white ;
    transition: all 0.5s ease-in-out;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  
  }

  .maindiv{
    text-decoration: none;
    /* color: #820000 ; */
    font-weight: 600;
   
    padding: 0.5rem 1rem;
    border: 2px solid transparent;
    border-radius: 5px;
  }
  .maindiv:hover{
    border-bottom: 2px solid  #8c0c3c;
    /* background-color:  white; */
  }
  
  .navbarMainResponse {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  #two {
    display: flex;
    font-size: large;
    margin-right: 2rem;
    font-weight: bolder;
  }
  #two .maindiv {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0.5rem;
    font-weight:bold;
    transition: all 1s  ease-in-out;
  }
  
  #three {
    color: rgb(0, 178, 137);
  }
  #three button {
    border: 1px solid rgb(0, 178, 137);
    font-size: large;
    padding: 0.25rem 0.5rem;
    border-radius: 5px;
    transition: 1s all;
    margin: 1rem 0.5rem;
  }
  #three button:hover {
    background-color: rgb(0, 178, 137);
    color: white;
  }
  #three button:last-child {
    background-color: rgb(0, 178, 137);
    color: white;
  }
  

  #upBtn{
    position: fixed;
    /* color: #820000; */
    background-color: transparent;
    border: 2px solid  transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.2rem;
    transition: all 0.5s ease-out ;
   top: 85%;
   left: 90%;
    
  }

  #upBtn:hover {
   
    color: white;
    background-color: #8c0c3c;
    border: 2px solid  #8c0c3c;
  
  }


  @media screen and (max-width: 800px) {
  
      .menuSmallPage{
          width: 100%;
          position: fixed;
          margin: 2rem 0;
          background-color: rgb(255, 255, 255);
          /* border: 1px solid red; */
      }
  
      #two{
          display: flex;
          flex-direction: column;
      }
  
      #three{
          display: flex;
          flex-direction: column;
      }
  }
  