.techStackContainer{
    width: 80%;
    background-color: transparent;
    display: grid;
    /* margin: 0.75rem auto; */
    margin: auto;

    grid-template-columns: repeat(5,1fr);
    gap: 1rem;
    /* padding: 0.75rem 0; */
    align-items: center;
}


.techStackContainer .cardTech{
        max-width: 150px;
        min-width: 150px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: auto;
        padding: 10px 8px;
        border-radius: 15px;
        transition: all 0.5s ease ;
        /* border: 1px solid red; */
        /* padding: 0.5rem; */
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
       
}
.cardTech h3{
   font-weight:400 ;
   text-transform: capitalize;
}
.cardTech:hover{
   transform: scale(1.2);
   border:1px solid #8c0c3c;
}
.techStackContainer .card img{

  width: 80px;
        

}


@media all and (max-width: 800px) {
  


    .techStackContainer{
    
        grid-template-columns: repeat(2,1fr);
    }

  }